@media only screen and (min-width: 1080px){

  *{
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
  }
  
  a,li{
    text-decoration: none;
    list-style-type: none;
    color:aliceblue;
    font-size: 2.5rem;
    font-weight: 100;
  }
  
  a{
    color:aliceblue;
  }
  
  
  .laptop{
  position: fixed;
  top: 5vh;
  margin-top: -20px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  
  }
  
  ul{
    display: flex;
    width: 95vw;
  margin-right: 35px;
    height: 16vh;
  justify-content: space-around;
  align-items: center;
  background-color: #2A2F51;
  }
  
  body{
    height: 120vh;
  }
  
  img{
    width: 30vw;
  }
  
  .IMG{
    height: 15vh;
    width: 25vh;
  }

}


@media only screen and (max-width: 600px){
  *{
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
  }
  
  a,li{
    text-decoration: none;
    list-style-type: none;
    color:aliceblue;
    font-size: 2.5rem;
    font-weight: 100;
  }

  .laptop{
    display: none;
  }



}